	module.exports = {"data":{"site":{"siteMetadata":{"rootURL":"https://naoty.github.io"}},"markdownRemark":{"frontmatter":{"title":"Atom形式のフィードをサポートした","time":"2018-01-12T21:08:00+0900","description":"Atom形式のフィードをサポートした"},"html":"<p>Atom形式のフィードをサポートした。一覧画面と詳細画面の下部にあるリンクからアクセスできる。</p>\n<p>Livedoor Reader時代からずっとRSSリーダーを使っており、今はFeedlyを使っている。Twitterやはてなブックマークのタイムラインでは、時間の経過とともに見逃してしまうが、RSSリーダーであれば未読がどんどん積まれるだけなので見逃す心配がなく、それが使い続けている理由になっている。</p>\n<p>ブログを更新したとき自分でTwitterでリンクを公開するのがなんとなくこそばゆい気持ちになる。シェアしなければしないで、誰も気づかないだろうから、それも寂しい。だから、更新されたら勝手にフィードが生成されて通知される方が気が楽だ。これで他人の目を気にせずどんどん記事を更新できるし、読者との距離感を気にせずに済む。</p>","fileAbsolutePath":"contents/posts/13.md"}},"pathContext":{"id":"contents/posts/13.md absPath of file >>> MarkdownRemark"}}

